.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  margin: 20px 0;
}

.bingo-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bingo-row {
  display: flex;
}

.bingo-cell {
  width: 120px;
  height: 120px;
  border: 1px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.bingo-cell.clicked {
  background-color: #90ee90;
  color: #fff;
}

.submission-form {
  margin-top: 20px;
  text-align: left;
  max-width: 400px;
  margin: auto;
}

.submission-form label {
  display: block;
  margin-bottom: 10px;
}

.submission-form textarea {
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
}

.submission-form button {
  display: block;
  margin: auto;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.submission-form button:hover {
  background-color: #0056b3;
}
